import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Shop from '@/components/Shop'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop
    }
  ]
})



