<template>
  <div>
    <div class="ui vertical basic segment">
      <top-menu></top-menu>
      <div class="ui container user-message">
        <div class="ui message">
          <div class="header">Atenção</div>
          <p>
            Todos os produtos aqui estão em bom estado (a não ser que a descrição diga o contrário).
            Se o produto está "sem custo", o interessado fica responsável por vir até mim
            buscar o produto, pois trata-se de uma doação.
          </p>
          <p>
            Interessados, por gentileza, entrar em contato por telegram, twitter ou e-mail (preferencialmente).
            Vendas apenas para residentes de <strong>Fortaleza/CE</strong> ou quem possa vir buscar.
          </p>
        </div>
      </div>
    </div>
    <h3 class="ui horizontal header divider">Produtos</h3>
    <div class="ui container">
      <div class="ui cards">
        <shop-item :key="item.title" v-for="item in items" :item="item"></shop-item>
      </div>
    </div>
  </div>
</template>

<script>
import TopMenu from './TopMenu'
import ShopItem from './ShopItem'

export default {
  name: 'Shop',
  components: {TopMenu, ShopItem},
  data: function () {
    return {
      items: [
        {
          'title': 'Carrinho com Motor',
          'description': '',
          'price': 1000,
          'image': require('@/assets/carrinho-maker.jpg'),
          'available': true
        }
      ]
    }
  }
}
</script>

<style scoped>
h3 { margin-top: 1em; }
.user-message { margin-top: 1em; }
</style>



