import Vue from 'vue'
import App from './App'
import router from './router'

require('@/resources/semantic-ui/semantic.css')
require('@/resources/semantic-ui/semantic.js')

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



