<template>
  <div class="card">
    <div class="image">
      <img :src="item.image" :alt="item.image_desc || 'indisponível'" />
    </div>
    <div class="content">
      <div class="header" v-text="item.title"></div>
      <div class="meta" v-text="item.description"></div>
    </div>
    <div class="extra content">
      <span>Preço</span>
      <strong class="floated right" title="produto disponível" v-if="item.available">
        R$ {{ fprice(item.price) }}
      </strong>
      <del class="floated right" title="já foi vendido" v-else>
        R$ {{ fprice(item.price) }}
      </del>
    </div>
  </div>
</template>

<script>
export default {
  name: 'shop-item',
  props: ['item'],
  methods: {
    fprice: function (value) {
      return (value / 100).toFixed(2).replace('.', ',')
    }
  }
}
</script>



